import React from 'react';

import classes from './Main3.module.css';
import ferro from '../../assets/UI/ferro.svg';

const Main3 = ({ onSetPonte }) => {
    return (
        <div id="main3" className={classes.main3}>
            <div className={classes.title}>
                <img src={ferro} alt="Ponte de Ferro" />
            </div>
            <div className={classes.info} id="info2">
                <div className={classes.dressImgs}>
                    <div className={classes.dressImg1}></div>
                    <div className={classes.dressDiv}>
                        <div className={classes.dressImg2}></div>
                        <div className={classes.dressImg3}></div>
                    </div>
                </div>
                <div className={classes.text}>
                    <div>
                        <p>A Ponte da Boa Vista em Recife, Brasil,  uma estrutura histrica com mais de 300 anos de existncia, tendo passado por diversas reconstrues ao longo de sua histria. Ela desempenhou um papel crucial na mobilidade da cidade, na conectividade entre bairros e testemunhou eventos histricos importantes.</p>
                        <p>Atualmente,  um marco arquitetnico e cultural, atraindo turistas e mantendo sua relevncia na identidade de Recife. Sua preservao contnua  fundamental para manter seu valor histrico e cultural.</p>
                    </div>
                    <button
                        className={classes.saibaMais}
                        onClick={() => {
                            onSetPonte('ferro');
                        }}
                    >
                        SAIBA MAIS
                    </button>
                </div>
            </div>
        </div>
    );
};

export default Main3;
